@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

// || GRADIENT CLASSES

.default-gradient {
  background-image: linear-gradient(
    to bottom,
    #000000,
    #3b141e,
    #731b2d,
    #ad2234,
    #e73131
  );
}

.blue-gradient {
  // background-image: linear-gradient(
