body {
  font-family: "Roboto", sans-serif;
  color: #fff;
  overflow: hidden;
}

.page__wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.container {
  min-height: 100vh;
  position: relative;
}

.centerpiece__container {
  display: none;
}

.particleBin {
  // border: 1px solid #fff;
  display: flex;
  gap: 1.4em;
  justify-content: space-between;
  padding: 0 0.5em;
}

.particle {
  width: 0.3rem;
  height: 1rem; // pattern options: 0.5rem, 0.7rem, 1rem
  background-color: #fff;
  opacity: 0; // default value 0.6
  border-radius: 50%;
  animation-duration: 4.5s; // speed options: 0.5s, 2.5s, 4.5s, 6.5s, 8.5s
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

.bottom {
  animation-name: particles;
}

.top {
  animation-name: particles_reverse;
}

// || BOTTOM PARTICLES

.bottom:nth-child(2) {
  animation-delay: 0.2s;
}
.bottom:nth-child(3) {
  animation-delay: 0.4s;
}
.bottom:nth-child(4) {
  animation-delay: 0.6s;
}
.bottom:nth-child(5) {
  animation-delay: 0.8s;
}
.bottom:nth-child(6) {
  animation-delay: 1s;
}
.bottom:nth-child(7) {
  animation-delay: 1.2s;
}
.bottom:nth-child(8) {
  animation-delay: 1.4s;
}
.bottom:nth-child(9) {
  animation-delay: 1.6s;
}
.bottom:nth-child(10) {
  animation-delay: 1.8s;
}
.bottom:nth-child(11) {
  animation-delay: 2s;
}
.bottom:nth-child(12) {
