.phone-up,
.tablet-landscape-up {
  display: none;
}

// Animation
@keyframes particles {
  0% {
    transform: translateY(0%);
  }

  50% {
    opacity: 0.5;
    // background-color: rgb(241, 118, 110);
  }
  100% {
    transform: translateY(-5000%);
    opacity: 0;
  }
}

@keyframes particles_reverse {
  0% {
    transform: translateY(0%);
  }
  50% {
    opacity: 0.5;
    // background-color: rgb(241, 118, 110);
  }
  100% {
    transform: translateY(5000%);
    opacity: 0;
  }
}

@keyframes hoverMe__fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

p {
  text-transform: uppercase;
  margin-top: 1.5rem;
}

p.hoverMe {
  animation: hoverMe__fadeIn 4s 2s forwards;
}

// media queries

@mixin for-tablet-portrait-up {
  @media (min-width: 600px) {
    @content;
  }
}

@mixin for-tablet-landscape-up {
  @media (min-width: 900px) {
    @content;
  }
}

@include for-tablet-portrait-up {
  .phone-up {
    display: block;
  }
}

@include for-tablet-landscape-up {
  .tablet-landscape-up {
    display: block;
  }

  .centerpiece__container {
    display: grid;
    margin: auto;
    place-items: center;
  }

  .centerpiece__container > * {
    opacity: 0;
    transition: 0.4s all;
  }

  .centerpiece__container:hover > * {
    opacity: 1;
  }

  .centerpiece__item {
    display: flex;
    gap: 2em;
    align-items: center;
    justify-content: space-between;
    // border: solid 1px #fff;
  }

  .color-option {
    display: grid;
    place-items: center;
    height: 2.8rem;
    width: 2.8rem;
    border-radius: 50%;
    border: solid 1px #ffffff00;
    transition: 0.4s all;
    &:hover {
      cursor: pointer;
      border-color: #fff;
    }
  }
  .color-option__1 {
    background-image: radial-gradient(
      circle,
      #ad2234,
      #e73131,
      #731b2d,
      #3b141e
    );
  }
  .color-option__2 {
    background-image: radial-gradient(
      circle,
      #0968f8,
      #0051c0,
      #003b8b,
      #002558,
      #020f2a
    );
  }
  .color-option__3 {
    background-image: radial-gradient(
      circle,
      #00ff85,
      #00c169,
      #00864c,
      #00502e,
      #011f11
    );
  }
  .color-selected {
    border: solid 1px #fff;
  }

  .speed-option,
  .motion-option {
    min-height: 1.2rem;
    width: 1.2rem;
    border-radius: 50%;
    background-color: #fff;
    transition: 0.2s all;
    border: solid 3px transparent;
    &:hover,
    &:active {
      cursor: pointer;
      border: solid 3px #fff;
      background-color: rgb(255, 0, 0);
    }
  }

  .speed-selected,
  .motion-selected {
    background-color: rgb(255 0 0);
    border: solid 3px #fff;
  }

  .speedWrap,
  .motionWrap {
    position: relative;
  }

  .speedLine,
  .motionLine {
    display: none; // disabled for right now
    position: absolute;
    width: 96%;
    top: 47%;
    bottom: 50%;
    left: 1%;
    margin: 0;
    color: #fff;
    z-index: -1;
    border: solid 1px;
  }
}
